.banner {
  height: 648px;
  color: white;
  object-fit: contain;
}

.banner__contents {
  margin-left: 50px;
  padding-top: 200px;
  height: 200px;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.7rem;
}

.banner__description {
  width: 45rem;
  line-height: 1.5;
  padding-top: 1.3rem;
  font-size: 0.8rem;
  max-width: 400px;
  height: 80px;
}

.banner--fadeBottom {
  height: 19.9rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}

.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
}

.banner__button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}
